import { Component, OnInit } from '@angular/core';
import { environment, eMenuAdmin } from 'src/environments/environment';
import { MainAdminComponent } from '../main-nav.component';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss'],
})
export class DashboardComponent implements OnInit {
  constructor(private __mainComponent: MainAdminComponent) {
    localStorage.setItem(
      environment.key_storage.MENU.ADMIN,
      eMenuAdmin[eMenuAdmin.DASHBOARD]
    );
    this.__mainComponent.__localStorage = localStorage.getItem(
      environment.key_storage.MENU.ADMIN
    );
  }

  ngOnInit(): void {}
}
